import React from "react";
import useTypewriterEffect, {
    getTypewriter,
    useCursor,
} from "use-typewriter-effect";

function Typed() {
    const [state, dispatch, isTyping] = useTypewriterEffect();
    const cursor = useCursor(isTyping);

    React.useEffect(() => {
        getTypewriter(dispatch)
            .type("Elijo Hacer")
            .pauseFor(1000)
            .deleteAll()
            .type("Evolucion Joven")
            .pauseFor(1000)
            .deleteAll()
            .type("El Futuro es Ahora")
            .pauseFor(1000)
            .deleteAll()
            .setLoop(true)
            .trigger()
    }, [dispatch]);

    const sentences = state.split("\n");
    const lastSentence = sentences.pop();
    return (
        <main>

            <div style={{color: "#43bafc"}}>
                {sentences.map((sen, i) => (
                    <p key={i}> {sen}</p>
                ))}
                <p>
                    <span style={{color: '#fff'}}>⠀</span> {lastSentence}
                </p>
            </div>
        </main>
    );
}

export default Typed;