import React from 'react';

import Carousel from "react-elastic-carousel";
import "./styles.css";
import { cards } from '../../data';
import Card from "../Cards/Card";


const breakPoints = [
  { width: 1, itemsToShow: 1 },
  { width: 550, itemsToShow: 2 },
  { width: 768, itemsToShow: 3 },
  { width: 1200, itemsToShow: 4 },
];

const CarouselComponent = () => {
  return (
    <section className='bg-tertiary py-12'>
      <div className='container mx-auto'>
        {/* <h1 style={{ textAlign: "center" }}>Example to setup your carousel in react</h1> */}
        <div className="App">
          <Carousel breakPoints={breakPoints}>
            {cards.map(({ name, image, text, ocupacion, ciudad, edad, facebook, twitter, instagram, id }) => (
              <Card image={image} name={name} text={text} ciudad={ciudad} edad={edad} ocupacion={ocupacion} facebook={facebook} twitter={twitter} instagram={instagram} />
            ))}
          </Carousel>
        </div>
      </div>
    </section>
  );
};

export default CarouselComponent;
