import React from "react";
import './card.css';
import PropTypes from "prop-types";


function Card({ image, name, text, ciudad, edad, ocupacion, facebook, twitter, instagram }) {
  return (

          <div class="card">
            <div>
              <img src={image} alt="" />
              <h1>
                {name}
                <br></br>
                <span>{ciudad}<span> - </span><span>{edad}</span></span>
              </h1>
              <p>{ocupacion}</p>
              <p>{text}</p>
              <ul>
                {/* <li><a href=""><i class="fab fa-linkedin-in"></i></a></li> */}
                <li><a href={facebook}><i class="fab fa-facebook-f" target='_blank'></i></a></li>
                <li><a href={twitter}><i class="fab fa-twitter" target='_blank'></i></a></li>
                <li><a href={instagram}><i class="fab fa-instagram" target='_blank'></i></a></li>
              </ul>
              {/* <a class="btn" href="">Hire Me</a> */}
            </div>
          </div>
        );
}

Card.propTypes = {
  name: PropTypes.string.isRequired,
  text: PropTypes.string,
  twitter: PropTypes.string,
  image: PropTypes.string,
  instagram: PropTypes.string,
  facebook: PropTypes.string,
  ocupacion: PropTypes.string,
  ciudad: PropTypes.string,
  edad: PropTypes.string,
};

export default Card;


